.content {
  margin-bottom: -2.25vw;

  .home-page {
    background-color: white;

    .search-field {
      position: relative;

      & > img {
        width: 100%;
        height: 38vw;
        object-fit: cover;

        @include responsive(desktop-xs) {
          height: 45vw;
        }

        @include responsive(tablet) {
          height: 470px;
        }
      }

      &-container {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 7.5vw;

        @include responsive(desktop-xs) {
          padding: 5vw;
        }

        .join-glosfy {
          height: 13.3vw;
          display: flex;
          justify-content: space-between;
          color: #aaacab;

          @include responsive(desktop-xs) {
            flex-direction: column;
            height: auto;
          }

          .left-side {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 18.75vw;

            @include responsive(desktop-m) {
              width: 28vw;
            }

            @include responsive(desktop-xs) {
              width: 100%;
            }
            
            span {
              font-size: 0.94vw;
              line-height: 1.56vw;

              @include responsive(desktop-m) {
                font-size: 1.3vw;
              }

              @include responsive(desktop-xs) {
                font-size: 16px;
                margin-bottom: 1.5em;
              }

              @include responsive(tablet) {
                font-size: calc(10px + 0.5vw);
                margin-bottom: 15px;
              }

              &:nth-child(1) {
                font-size: 1.25vw;
                color: white;

                @include responsive(desktop-m) {
                  font-size: 1.8vw;
                }

                @include responsive(desktop-xs) {
                  font-size: 20px;
                }

                @include responsive(tablet) {
                  font-size: calc(14px + 0.5vw);
                  line-height: 20px;
                  margin-bottom: 20px;
                }
              }
            }
          }

          .right-side {
            display: flex;
            align-self: flex-end;
            font-size: 2.08vw;
            letter-spacing: 0.31vw;
            color: white;

            @include responsive(desktop-m) {
              font-size: 2.28vw;
            }

            @include responsive(desktop-xs) {
              width: 100%;
              font-size: 30px;
            }

            @include responsive(tablet) {
              font-size: calc(16px + 0.5vw);
            }
          }
        }

        .search-input-section {
          display: flex;
          justify-content: space-between;
          height: 5.16vw;
          
          @include responsive(desktop-xs) {
            height: 48px;
            flex-direction: row-reverse;
          }

          @include responsive(tablet) {
            flex-direction: column-reverse;
            flex-wrap: wrap;
            height: auto;
            margin: 15px 0;
          }

          .join-btn {
            width: 15vw;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.88vw;
            font-weight: bold;
            letter-spacing: 0.1vw;
            color: white;
            background-color: #ff003c;
            box-shadow: 0 0.31vw 2.34vw 0.78vw #6e0232;
            cursor: pointer;

            @include responsive(desktop-xs) {
              width: 136px;
              box-shadow: 0 3px 21px 7px #6e0232;
            }

            @include responsive(tablet) {
              width: 100%;
              height: 30px;
              margin-top: 15px;
              font-size: 14px;
            }
          }

          .search-input {
            width: 58vw;
            margin-bottom: 0;
            padding-left: 2.34vw;

            @include responsive(desktop-xs) {
              width: calc(100% - 180px);
              padding-left: 25px;
            }

            @include responsive(tablet) {
              width: 100%;
              flex-direction: column;
              padding: 0 25px;
              background-color: #ffffff1a;
              box-shadow: none;
            }

            li {
              width: 15vw;
              padding-top: 1.2vw;

              @include responsive(desktop-m) {
                padding-top: 1.2vw;
              }

              @include responsive(desktop-xs) {
                /** width: calc((100% - 150px) / 3); **/
                padding-top: 8px;
              }

              @include responsive(tablet) {
                width: 100%;
              }

              .geosuggest {
                input {
                  /** height: calc(1.5em + .75rem + 2px); **/

                  @include responsive(desktop-xs) {
                    height: 30px;
                  }

                  @include responsive(tablet) {
                    background-color: transparent;
                  }
                }
              }

              input {
                font-size: 0.94vw !important;
                font-weight: normal;
                padding: 0 !important;

                @include responsive(desktop-s) {
                  font-size: 1.2vw !important;
                }

                @include responsive(desktop-xs) {
                  font-size: 14px !important;
                }

                &::placeholder {
                  color: #838383;
                }
              }

              .dropdown-icon {
                display: flex;
                width: 1.04vw;
                height: 1.04vw;

                @include responsive(desktop-s) {
                  width: 1.5vw;
                  height: 1.5vw;
                }

                @include responsive(desktop-xs) {
                  width: 14px;
                  height: 14px;
                }

                img {
                  width: 100%;
                  height: 100%;
                }
              }
            }

            .search-btn {
              width: 5.16vw;
              display: flex;
              justify-content: center;
              align-items: center;
              background-color: #4743ed;
              border: none;

              @include responsive(desktop-xs) {
                width: 48px;
              }

              @include responsive(tablet) {
                width: 100%;
                height: 30px;
                margin: 15px 0;
              }

              img {
                width: 1.87vw;

                @include responsive(desktop-xs) {
                  width: 17px;
                }
              }
            }
          }
        }
      }
    }

    .popular-events-container {
      .location-label {
        display: flex;
        font-size: 1.875vw;
        font-weight: bold;
        letter-spacing: 1.5px;
        color: black;
        padding: 3.3vw 0 3.3vw 20.6vw;

        @include responsive(desktop-s) {
          font-size: 2vw;
          padding: 3.3vw 0 3.3vw 15vw;
        }

        @include responsive(desktop-xs) {
          font-size: 20px;
          padding: 25px 0 25px 12%;
        }

        @include responsive(tablet) {
          font-size: calc(14px + 0.5vw);
          padding: calc(15px + 0.5vw) 0 calc(15px + 0.5vw) 10%;
        }

        .location-value {
          display: flex;
          flex-direction: column;
          color: #4743ed;
          padding-left: 0.8vw;

          &:after {
            content: '';
            width: 100%;
            height: 1px;
            background-color: #000000;
          }
        }
      }

      .slick-slider {
        height: 30vw;
        padding-left: 0.47vw;

        @include responsive(tablet) {
          height: 70vw;
        }

        .slick-track {
          & > div {
            margin-right: 0.47vw;
          }

          & > div:nth-child(1) {
            width: 27.2vw !important;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
  
            .event-item {
              height: 14.5vw;
            }
          }

          & > div:nth-child(2) {
            width: 45.5vw !important;
          }
  
          & > div:nth-child(3) {
            width: 24vw !important;
          }

          @include responsive(tablet) {
            .event-item {
              height: 70vw !important;
            }
          }
        }

        .slider-one {
          height: 30vw;
          display: flex !important;
          flex-direction: column;
          justify-content: space-between;
        }

        .slider-two,
        .slider-three {
          height: 30vw;
        }
      }
    }

    .event-item {
      position: relative;
      color: white;

      @include responsive(desktop-xs) {
        height: 30vw;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &-content {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .item-header {
          display: flex;
          justify-content: flex-end;

          .control-btns {
            display: flex;
            flex-direction: column;
            padding: 0.94vw 0.94vw 0;

            @include responsive(desktop-xs) {
              padding: 15px 15px 0;
            }

            & > div {
              width: 2.5vw;
              height: 2.5vw;
              border-radius: 50%;
              cursor: pointer;

              @include responsive(desktop-xs) {
                width: 30px;
                height: 30px;
              }

              @include responsive(tablet) {
                width: calc(30px + 0.5vw);
                height: calc(30px + 0.5vw);
              }
            }

            div:first-child {
              margin-bottom: 0.94vw;
            }

            .control-share-btn {
              background-color: #4743ed;
              background-image: url('../images/share.png');
              background-repeat: no-repeat;
              background-size: 60%;
              background-position: center;

              &:hover {
                background-color: white;
                background-image: url('../images/share-hover.png');
              }
            }

            .control-like-btn {
              background-color: white;
              background-image: url('../images/like.png');
              background-repeat: no-repeat;
              background-size: 60%;
              background-position: center;

              &.liked {
                background-image: url('../images/like-hover.png');
              }

              &:hover {
                background-image: url('../images/like-hover.png');
              }
            }
          }
        }

        .event-infos {
          position: absolute;
          bottom: 0;
          display: flex;
          flex-direction: column;
          margin: 4% 6%;

          @include responsive(desktop-xs) {
            margin: 0 0 20px 25px;
          }

          @include responsive(tablet) {
            margin: 0 0 5% 5%;
          }

          .event-title {
            width: 70%;
            font-size: 1.25vw;
            font-weight: bold;
            line-height: 1.5vw;
            color: #ffffff;

            @include responsive(desktop-s) {
              width: 85%;
              font-size: 1.5vw;
              line-height: 2vw;
            }

            @include responsive(desktop-xs) {
              width: 100%;
              font-size: 18px;
              line-height: 22px;
            }

            @include responsive(tablet) {
              font-size: calc(16px + 0.5vw);
            }
          }

          .event-date {
            font-size: 0.833vw;
            margin-top: 0.2vw;

            @include responsive(desktop-s) {
              font-size: 1vw;
            }

            @include responsive(desktop-xs) {
              font-size: 14px;
            }

            @include responsive(tablet) {
              font-size: calc(12px + 0.5vw);
            }
          }

          .event-time {
            font-size: 0.833vw;

            @include responsive(desktop-s) {
              font-size: 1vw;
            }

            @include responsive(desktop-xs) {
              font-size: 14px;
            }

            @include responsive(tablet) {
              font-size: calc(12px + 0.5vw);
            }
          }

          .event-description {
            width: 75%;
            margin-top: 1vw;
            font-size: 0.94vw;

            @include responsive(desktop-s) {
              font-size: 1vw;
            }

            @include responsive(desktop-xs) {
              width: 100%;
              font-size: 13px;
            }

            @include responsive(tablet) {
              font-size: calc(12px + 0.5vw);
            }
          }
        }
      }
    }

    .mobile-download-container {
      display: flex;
      justify-content: space-between;
      width: 58.12vw;
      margin: 11.25vw auto;

      @include responsive(desktop-s) {
        margin: 8vw auto;
      }

      @include responsive(desktop-xs) {
        width: 500px;
        margin: 60px auto;
      }

      @include responsive(tablet) {
        flex-direction: column;
        align-items: center;
        width: 90vw;
        margin: calc(20px + 0.5vw) 5vw calc(40px + 0.5vw);
      }

      .mobile-img {
        width: 26.4vw;
        height: 45.3vw;
        margin-left: -2.5vw;

        @include responsive(desktop-xs) {
          width: 230px;
          height: 390px;
          margin-left: -20px;
        }

        @include responsive(tablet) {
          width: 50vw;
          height: 84.8vw;
          margin-left: 0;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      .download-section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 31.41vw;

        @include responsive(desktop-xs) {
          width: 270px;
        }

        @include responsive(tablet) {
          width: 90vw;
        }

        .download-header {
          color: #05052b;
          margin-bottom: 6vw;

          @include responsive(desktop-s) {
            margin-bottom: 4.5vw;
          }

          @include responsive(desktop-xs) {
            margin-bottom: 38px;
          }

          @include responsive(tablet) {
            margin-bottom: calc(20px + 0.5vw);
          }

          & > div:nth-child(1) {
            font-size: 3.125vw;
            font-weight: bold;
            letter-spacing: 0.3vw;

            @include responsive(desktop-s) {
              font-size: 4vw;
            }

            @include responsive(desktop-xs) {
              font-size: 34px;
            }

            @include responsive(tablet) {
              font-size: calc(26px + 0.5vw);
            }
          }

          & > div:nth-child(2) {
            font-size: 1.25vw;
            letter-spacing: 0.15vw;
            
            @include responsive(desktop-s) {
              font-size: 1.6vw;
            }

            @include responsive(desktop-xs) {
              font-size: 14px;
              letter-spacing: 1.2px;
            }

            @include responsive(tablet) {
              font-size: calc(14px + 0.5vw);
            }
          }
        }

        .download-btns {
          display: flex;
          
          & > div:nth-child(1) {
            width: 15.47vw;
            height: 5.16vw;

            @include responsive(desktop-xs) {
              width: 133px;
              height: 44.5px;
            }

            @include responsive(tablet) {
              width: 43vw;
              height: 17vw;
            }

            img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }

          & > div:nth-child(2) {
            width: 15vw;
            height: 5.16vw;
            margin-left: 0.94vw;

            @include responsive(desktop-xs) {
              width: 129px;
              height: 44.5px;
              margin-left: 8px;
            }

            @include responsive(tablet) {
              width: 42vw;
              height: 17vw;
              margin-left: 5vw;
            }

            img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }
        }
      }
    }

    .featured-events-container {
      display: flex;
      flex-direction: column;
      width: 58.12vw;
      margin: 0 auto;

      @include responsive(desktop-s) {
        width: 70vw;

        .event-item {
          &-content {
            .event-infos {
              width: 88%;

              .event-title {
                width: 100%;
              }
            }
          }
        }
      }

      @include responsive(desktop-xs) {
        width: 80vw;
      }

      @include responsive(tablet) {
        width: 90vw;
      }

      .featured-events-title {
        font-size: 1.875vw;
        font-weight: bold;
        letter-spacing: 0.2vw;
        padding-bottom: 3.75vw;

        @include responsive(desktop-s) {
          font-size: 2vw;
        }

        @include responsive(desktop-xs) {
          font-size: 20px;
          letter-spacing: 1.5px;
          padding-bottom: 30px;
        }

        @include responsive(tablet) {
          font-size: calc(14px + 0.5vw);
          padding-bottom: calc(20px + 0.5vw);
        }
      }

      .events-content {
        display: flex;
        flex-wrap: wrap;

        .event-item {
          margin: 0 0.75vw 0.75vw 0;

          &:nth-child(6n+1),
          &:nth-child(6n+6)
           {
            width: 37.16vw;
            height: 19.67vw;

            @include responsive(desktop-s) {
              width: 45vw;
              height: 23.82vw;
            }

            @include responsive(desktop-xs) {
              width: 50vw;
              height: 26.5vw;
            }
          }

          &:nth-child(6n+2),
          &:nth-child(6n+5)
           {
            width: 19.12vw;
            height: 19.67vw;

            @include responsive(desktop-s) {
              width: 23.5vw;
              height: 23.82vw;
            }

            @include responsive(desktop-xs) {
              width: 28.5vw;
              height: 26.5vw;
            }

            .event-title {
              width: 100%;
            }
          }

          &:nth-child(6n+3) {
            width: 27.19vw;
            height: 15vw;

            @include responsive(desktop-s) {
              width: 36.5vw;
              height: 20.13vw;
            }

            @include responsive(desktop-xs) {
              width: 41.5vw;
              height: 23vw;
            }
          }

          &:nth-child(6n+4) {
            width: 29.06vw;
            height: 15vw;

            @include responsive(desktop-s) {
              width: 32vw;
              height: 20.13vw;
            }

            @include responsive(desktop-xs) {
              width: 37vw;
              height: 23vw;
            }
          }

          @include responsive(tablet) {
            width: 90vw !important;
            height: 40vw !important;
            margin: 0 0 3vw 0;
          }
        }
      }

      .more-events {
        display: flex;
        justify-content: flex-end;
        font-size: 1.25vw;
        letter-spacing: 0.2vw;
        padding: 3.28vw 0.85vw 3.28vw 0;

        @include responsive(desktop-s) {
          font-size: 1.8vw;
          letter-spacing: 0.15vw;
        }

        @include responsive(desktop-xs) {
          font-size: 20px;
          letter-spacing: 1.4px;
          padding: 25px 0.85vw;
        }

        @include responsive(tablet) {
          font-size: calc(12px + 0.5vw);
          padding: calc(15px + 0.5vw) 0;
        }
      }
    }

    .subscribe-form {
      display: flex;
      flex-direction: column;
      width: 40vw;
      margin: 4.22vw auto 0 auto;

      @include responsive(desktop-s) {
        width: 45vw;
      }

      @include responsive(desktop-xs) {
        width: 60vw;
      }

      @include responsive(tablet) {
        width: 80vw;
      }

      p {
        font-size: 1.25vw;
        font-weight: bold;
        letter-spacing: 0.15vw;
        text-align: center;
        padding-bottom: 1.87vw;

        @include responsive(desktop-s) {
          font-size: 1.8vw;
        }

        @include responsive(desktop-xs) {
          font-size: 2.5vw;
        }

        @include responsive(tablet) {
          font-size: calc(18px + 0.5vw);
        }
      }

      .form-content {
        display: flex;
        width: 100%;
        height: 5.16vw;
        box-shadow: 0 2.5vw 1.56vw 0.58vw #19061c99;

        @include responsive(desktop-xs) {
          height: 6.5vw;
        }

        @include responsive(tablet) {
          height: 11.5vw;
        }

        .input-content {
          display: flex;
          justify-content: center;
          width: 56%;
          background: white;

          input {
            width: 80%;
            height: 3vw;
            font-size: 0.94vw;
            outline: none;
            padding: 0 1vw;
            border: none;
            border-bottom: solid 1px #000000;
            margin-top: 1vw;

            @include responsive(desktop-s) {
              font-size: 1.5vw;
            }

            @include responsive(desktop-xs) {
              font-size: 2vw;
              margin-top: 1.8vw;
            }

            @include responsive(tablet) {
              font-size: calc(11px + 0.5vw);
              height: auto;
              margin: 1.5vw 0;
            }
          }
        }

        .subscribe-btn {
          width: 44%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 1.56vw;
          font-weight: bold;
          letter-spacing: 0.15vw;
          color: white;
          background-color: #4743ed;
          cursor: pointer;

          @include responsive(desktop-xs) {
            font-size: 2.2vw;
            letter-spacing: 0.3vw;
          }

          @include responsive(tablet) {
            font-size: calc(12px + 0.5vw);
          }
        }
      }
    }
  }
}