.footer {
  background-color: #05052b;

  &-sides {
    width: 63vw;
    background-color: #05052b;
    margin: 6.56vw auto 5.16vw;

    .footer-side-menu {
      display: flex;
      flex-direction: column;
      align-items: center;

      .logo-img {
        width: 10.54vw;
        height: 2.66vw;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      .footer-side-panel {
        height: 100%;
        padding-top: 0.4vw;

        p {
          font-size: 0.94vw;
          font-weight: bold;
          letter-spacing: 0.1vw;
          color: white;
        }

        ul {
          margin-bottom: 0;

          li {
            font-size: 0.85vw;
            color: #e1e1e3;
            margin-bottom: 0.56vw;

            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }

      .social {
        padding-top: 0;

        p {
          font-size: 0.94vw;
          font-weight: bold;
          letter-spacing: 0.1vw;
          color: white;
          text-align: center;
          margin: 1.4vw 0 2.34vw;
        }

        ul {
          display: flex;
          margin-bottom: 2.06vw !important;

          li {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2.5vw;
            height: 2.5vw;
            border-radius: 50%;
            background-color: white;
            margin-right: 0.8vw;
            margin-bottom: 0 !important;

            &::after {
              display: none;
            }

            &:last-child {
              margin-right: 0;
            }

            a {
              display: flex;

              i {
                font-size: 1.6vw;
                font-weight: bold;
                color: #000000;
              }
            }
          }
        }
      }

      .download-app {
        width: 100%;
        display: flex;
        justify-content: space-between;

        & > div {
          width: 7.5vw;
          height: 2.44vw;

          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
      }
    }
  }

  &-center-menu {
    height: 5.16vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0a0826;

    .footer-menu {
      width: 61.12vw;
    }
  }

  @include responsive(desktop-s) {
    &-sides {
      width: 80vw;

      .footer-side-menu {
        .logo-img {
          width: 12.5vw;
          height: 3.6vw;
        }

        .footer-side-panel {
          p {
            font-size: 1.4vw;
          }

          ul {
            li {
              font-size: 1.2vw;
            }
          }
        }

        .social {
          ul {
            li {
              width: 3.5vw;
              height: 3.5vw;

              a i {
                font-size: 2vw;
              }
            }
          }
        }

        .download-app {
          & > div {
            width: 10.5vw;
            height: 3.2vw;
          }
        }
      }
    }

    &-center-menu {
      .footer-menu {
        width: 80vw;
      }
    }
  }

  @include responsive(desktop-xs) {
    &-sides {
      flex-wrap: wrap;

      & > div:first-child {
        width: 100%;
        flex-direction: row;
        align-items: center;
        margin-bottom: 4vw;

        .logo-img {
          width: 25.5vw;
          height: 4.5vw;
        }

        .social {
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;

          p {
            font-size: 1.6vw !important;
            margin: 0;
          }

          ul {
            margin: 0 !important;
            margin-left: 2vw !important;
          }
        }

        .download-app {
          width: auto;
          justify-content: flex-end;
        }
      }

      .footer-side-menu {
        .footer-side-panel {
          p {
            font-size: 2vw;
          }

          ul {
            li {
              font-size: 1.6vw;
            }
          }
        }
      }
    }

    &-center-menu {
      height: auto;

      .footer-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0.5em 0;

        p {
          padding: 0.4em 0;
        }

        ul {
          padding: 0.4em 0;
        }
      }
    }
  }

  @include responsive(tablet) {
    padding: 0 5vw;

    &-sides {
      width: 90vw;
      margin: 6.56vw 0 5.16vw;

      & > div:first-child {
        flex-direction: column;
        margin-bottom: 30px;

        .logo-img {
          width: 50vw;
          height: 10vw;
          margin-bottom: calc(10px + 0.5vw);
        }

        .social {
          p {
            font-size: calc(12px + 0.5vw) !important;
          }

          ul {
            li {
              width: calc(20px + 0.5vw);
              height: calc(20px + 0.5vw);
              margin-right: 1.2vw;

              a i {
                font-size: calc(11px + 0.5vw);
              }
            }
          }
        }

        .download-app {
          width: 80vw;
          justify-content: center;

          & > div {
            width: 20vw;
            height: 7vw;
          }

          & > div:nth-child(2) {
            margin-left: 5vw;
          }
        }
      }

      .footer-side-panel {
        padding: 20px 10px;

        p {
          font-size: calc(14px + 0.5vw) !important;
          margin-bottom: 2vw;
        }

        ul {
          li {
            font-size: calc(10px + 0.5vw) !important;
            margin-bottom: 1vw !important;
          }
        }
      }
    }

    &-center-menu {
      margin: 0 -5vw;

      .footer-menu {
        width: 90vw;
        
        ul {
          margin: 0;
        }
      }
    }
  }
}