.navbar {
  height: 6vw;
  padding: 0 7.5vw;

  .nav-left {
    display: flex;
    align-items: center;

    .navbar-brand {
      img {
        width: 14vw;
        height: auto;
      }
    }
  }

  .search__block {
    width: 36.45vw;
    height: 3.125vw;
    margin: 0 3.75vw 0 auto;

    .advanced-search {
      border: none;
      width: calc(100% - 3.125vw);
      height: 3.125vw;
    }

    .search__btn {
      width: 3.125vw;
      height: 3.125vw;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #4743ed;
      cursor: pointer;

      i {
        font-size: 1.5625vw;
      }
    }
  }

  .navbar-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .language-option {
      width: 145px;

      img {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }

    .dropdown-icon {
      display: flex;
      width: 14px;
      height: 7px;

      img {
        width: 100%;
        height: 100%;
        border-radius: 0;
        margin: 0;
      }
    }

    .burger-menu {
      display: none;
      width: 25px;
      height: 25px;
      margin-left: 30px;

      .bm-burger-button {
        position: relative;
        width: 25px;
        height: 25px;

        .bm-burger-bars {
          background-color: black;
        }
      }

      .bm-overlay {
        top: 0;
        left: 0;
      }

      .bm-menu-wrap {
        width: 150px !important;

        .bm-item {
          background-color: #00000040;
          color: white;
          padding: 5px 0 5px 20px;
        }
      }

      @include responsive(tablet) {
        display: block;
      }
    }
  }

  @include responsive(desktop-m) {
    .search__block {
      width: 30vw;
    }
  }

  @include responsive(desktop-s) {
    .search__block {
      width: 25vw;
      margin: 0 2.5vw 0 auto;
    }

    .navbar-nav {
      .nav-item {
        margin-left: 1.5em;

        a {
          font-size: 14px;
        }
      }

      .language-option {
        width: 140px;

        img {
          width: 18px;
          height: 18px;
          margin-right: 8px;
        }
      }

      .dropdown-icon {
        display: flex;
        width: 14px;
        height: 7px;
  
        img {
          width: 100%;
          height: 100%;
          border-radius: 0;
          margin: 0;
        }
      }
    }
  }

  @include responsive(desktop-xs) {
    height: 55px;
    padding: 0 5%;

    .nav-left {
      .navbar-brand {
        img {
          width: 136px;
        }
      }
    }

    .search__block {
      width: 25%;
      height: 32px;
      margin-right: 1em;

      .advanced-search {
        width: calc(100% - 32px);
        height: 32px;
      }

      .search__btn {
        width: 32px;
        height: 32px;

        i {
          font-size: 14px;
        }
      }
    }

    .navbar-nav {
      .nav-item {
        margin-left: 1em;
      }
    }
  }

  @include responsive(tablet) {
    flex-direction: row !important;
    justify-content: space-between;
    height: 50px;
    
    .nav-left {
      margin-bottom: 0;

      .navbar-brand {
        img {
          width: 100px;
        }
      }
    }

    .search__block {
      display: none;
    }

    .navbar-nav {
      .language-option {
        width: 120px;

        img {
          width: 15px;
          height: 15px;
        }
      }

      .dropdown-icon {
        display: flex;
        width: 14px;
        height: 7px;
  
        img {
          width: 100%;
          height: 100%;
          border-radius: 0;
          margin: 0;
        }
      }

      .nav-item {
        display: none;
      }
    }
  }
}
